}

.pone,
.ptwo,
.pthree {
  position: relative;
}

.pone::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-image: url("backh.jpg");
  background-repeat: no-repeat;
  background-size: auto 100vh;
  background-position: center center;
  filter: brightness(15%);
  background-color: #ff898b;

}

.ptwo::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-image: url("backs.jpg");
  background-repeat: no-repeat;
  background-size: auto 100vh;
  background-position: center center;
  filter: brightness(20%);
  background-color: #75adfa;

}

.pthree::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-image: url("backa.jpg");
  background-repeat: no-repeat;
  background-size: auto 100vh;
  background-position: center center;
  filter: brightness(20%);
  background-color: #eed791;
}

