.ph::after {
  content: "";
  border-bottom: 5px dashed #2192b5;
  /* Use border-bottom to create a line under the text */
  position: absolute;
  /* Position the pseudo-element relative to the parent with position: relative */
  bottom: 0;
  /* Position it at the bottom of the parent */
  left: 0;
  /* Position it at the left of the parent (adjust as needed) */
  width: 2.5cm;

  /* Make the line span the entire width of the parent */
}

.sh::after {
  border-bottom: 5px dashed #9355b8;
}

