}

.Nav {
  padding: .5cm 5vw 0cm 5vw;
}

.NavHome {
  position: relative;
  color: white;
}

.NavHome::before {
  content: "";
  z-index: -1;
  top: 0;
  right: 0;
  bottom: 0;
  /* Add this line to cover the entire height of the parent */
  left: 0;
  position: absolute;
  background-color: #3a4c7d;
  filter: brightness(30%);
}


.start {
  background-color: transparent;
  border: 3px solid #00a0d9;
  background-color: #00a0d9;
  cursor: pointer;
  color: white;
  transition: .3s ease;
  margin-top: 1cm;
  font-family: acme;
  font-size: 25px;
  padding: .17cm .8cm;
  letter-spacing: .03cm;
  border-radius: 10px;

  &:hover {
    background-color: transparent;

  }
}

.pone-start {
  background-color: transparent;
  border: 3px solid #2192b5;

  &:hover {
    background-color: #2192b5;
  }
}

.ptwo-start {
  background-color: transparent;
  border: 3px solid #9355b8;

  &:hover {
    background-color: #9355b8;
  }
}
.pthree-start {
  background-color: transparent;
  border: 3px solid #edba51;

  &:hover {
    background-color: #edba51;
    color: black;
  }
}

@media only screen and (max-width:900px) {
  .Home::before {
    background-size: 95vw auto;
  }
}